.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background-image: radial-gradient(rgb(74, 0, 0), rgb(0, 0, 0));
    z-index:3;
  }
  
  .modal-main {
    position: relative;
    width: 80%;
    height: 80%;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    z-index:3;
  
  }
  
  .display-block {
    display: block;
  }
  
  .display-none {
    display: none;
  }
  .closeButton{
    position:fixed;
    top:0;
    left:0;
    cursor: pointer;
    z-index:3;
    background-color: rgba(0,0,0,.6);
    color: rgba(255,255,255,.8);
    font-size: 1em;
    border: #aaa;
  }
  
  .modal_info{
    display:flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    align-content: center;

  }
  .info_block{
    text-align: center;
    padding:10px;
    margin:10px;
    overflow: hidden;
    
  }
  .panel{
    display:flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }

  #donut{
    width:25%;
    height:25%;
    z-index:4;
    margin-top: 15px;

  }
  hr {
    margin-top: 5px;
    
    border: 0;
    clear:both;
    display:block;
    width: 60%;                
    background-color:white;
    height: 1px;
    overflow: hidden;
}
